import React from 'react'
import types from 'prop-types'
import './Form.css';
import aja from 'aja'

import {Check, Container, Options, Number, Decimal, Text} from './formComponents.js'
export class Form extends React.Component{
	constructor(props){
		super(props)
		this.state = {spec:null,dataArray:{}}
		this.fetchSpec = function(){
		  this.ajax = (this.ajax || aja())
		   .url(process.env.PUBLIC_URL+'/static/spec/'+this.props.formId+'.json')
		   .on('success',data=>{
			   this.setState({dataArray:fillEmpty(data)})
			   this.setState({spec:data})
		   })
		   .on('failure',err=>this.fetchSpec)
		   .go()
		}		
		this.toChange = pair=>{
			const newData = Object.assign({},this.state.dataArray,pair)
		    this.setState({dataArray:newData})
		}
	}
	componentDidMount(){
		this.fetchSpec()	
	}
	componentDidUpdate(prevProps,prevState){
		if(this.props.formId != prevProps.formId) this.fetchSpec()
	}
	componentWillUnmount(){
	  if(this.ajax){
		this.ajax.off("success")
		this.ajax.off("failure")
	  }
	}
	render(){
		
		return this.state.spec ? <div>
		  {this.state.spec.children.map(f=>(<
		    Generic onChange={this.toChange} field={f} dataArray={this.state.dataArray} 
	      />))}
		</div>: 'Форма завантажується ...'
	}
}

Form.proTypes = {
	formId : types.string
}

export class Generic extends React.Component{
	render(){
		const {num,type,dec,data,children} = this.props.field
		const value = this.props.dataArray[num]
		const toChange = nextValue=>this.props.onChange({[num]:nextValue})
		const common = {onChange:toChange,value,text:data}
		
		if(type == 'L' && children.length>0){  
			return <Container {...common} children={
				children.map(f=>(<Generic 
				  onChange={this.props.onChange} field={f} dataArray={this.props.dataArray}/>))
			}/>
		}else if(type == 'L'){
			return <Check {...common}  />
		}else if(type == 'V' && children.length>0){
			return <Options {...common} options={
				children.map(f=>({label:f.data,value:f.num}))
			}/>
		}else if(type == 'N' && dec || type == 'C'){
			return <Decimal {...common} dec={dec || 10} />
		}else if(type == 'N'){
			return <Number {...common} />
		}else if(type == 'T'){
			return <Text {...common} />
		}else{
			
			console.dir(this.props.field)
			throw new Error('Problems with your spec, wrong field.')
		}
		
	}
}

Generic.propTypes = {
	onChange: types.func.isRequired,
	field: types.object.isRequired,
	dataArray: types.object.isRequired
}

function fillEmpty(root){
	let result = {}
	const dic = {
		N:0,
		L:false,
		V:'',
		T:'',
		C:0
	}
    function trav(r){
		r.children.forEach(ch=>{
			result[ch.num]=dic[ch.type]
			trav(ch)
		})
	}
	trav(root)
	return result
}