import React from 'react'
import types from 'prop-types'
import {Checkbox, Radio, Collapse, InputNumber, Input} from 'antd'

export class Check extends React.PureComponent{
	render(){
		const {value,text} = this.props
		const toChange = e=>this.props.onChange(e.target.checked)
		return <Checkbox onChange={toChange} checked={value}>{text}</Checkbox>
	}
}

Check.propTypes = {
	onChange : types.func.isRequired,
	value : types.bool.isRequired,
	text : types.string.isRequired
}

export class Container extends React.PureComponent{
	render(){
		const {value,text,children} = this.props
		return <Collapse activeKey={value?1:[]} bordered={true}>
		  <Collapse.Panel header={<Check onChange={this.props.onChange}
		      {...{value,text}} />} showArrow={false} key={1}>
			{children}
		  </Collapse.Panel>
		</Collapse>
	}
}

Container.propTypes = {
	onChange : types.func.isRequired,
	value : types.bool.isRequired,
	text : types.string.isRequired,
	children : types.arrayOf(types.node).isRequired
}

export class Options extends React.PureComponent{
	render(){
		const {value,text,options} = this.props
		const toChange = e=>this.props.onChange(e.target.value)
		return <div>
		  <span>{text+':'}</span>
	      <Radio.Group onChange={toChange}{...{value, options}} /> 
		</div>
	}
}

Options.propTypes = {
	onChange : types.func.isRequired,
	value : types.any,
	text : types.string.isRequired,
	options : types.arrayOf(types.object).isRequired
}

export class Number extends React.PureComponent{
	render(){
	  const {onChange,value,text} = this.props
	  return <label>
	    <InputNumber onChange={onChange} value={value} precision={0} />
		{text}
	  </label>
	}
}

Number.propTypes = {
	onChange : types.func.isRequired,
	value : types.number.isRequired,
	text : types.string.isRequired,
}

export class Decimal extends React.PureComponent{
	render(){
	  const {onChange,value,text,dec} = this.props
	  return <label>
	    <InputNumber onChange={onChange} value={value} precision={dec} />
		{text}
	  </label>
	}
}

Decimal.propTypes = {
	onChange : types.func.isRequired,
	value : types.number.isRequired,
	text : types.string.isRequired,
	dec : types.number
}

export class Text extends React.PureComponent{
	render(){
      const {value,text} = this.props
	  const toChange = e=>this.props.onChange(e.target.value)
	  return <label>{text}
	    <Input.TextArea onChange={toChange} value={value} placeholder="..." 
		  maxLength={900} rows={3} /> 
		  
	  </label>
	}
}

Text.propTypes = {
	onChange : types.func.isRequired,
	value : types.string.isRequired,
	text : types.string.isRequired,
}
