import React from 'react';
import './App.css';
import aja from 'aja'

import {Form} from './Form.js'
import {Select} from 'antd'
const {Option} = Select

class App extends React.Component{
  constructor(props){
	super(props)
	this.state = {dic:{},formId:''}
	this.fetchDic = function(){
		  this.ajax = aja()
		   .url(process.env.PUBLIC_URL+'/static/spec/dic.json')
		   .on('success',data=>{
			   this.setState({dic:data})
		   })
		   .on('failure',err=>this.fetchData)
		   .go()
	}		
	this.toChange = value=>this.setState({formId:value})
  }
  componentDidMount(){
	this.fetchDic()	
  }
  componentWillUnmount(){
	if(this.ajax){
		this.ajax.off("success")
		this.ajax.off("failure")
	}
  }
  render(){
    let select = this.state.dic ? <Select showSearch onChange={this.toChange} 
	    size={"large"} placeholder={"Оберіть форму"}
    	style={{width:"100%", "margin-bottom":"24px", "margin-top":'24px'}} 
		    value={this.state.formId || undefined}>
			{Object.keys(this.state.dic).map(id=><Option key={id}>{this.state.dic[id]}</Option>)}
    </Select> : 'Завантажується список доступних форм ...'
	
	let form = this.state.formId ?
      <div className="Form">
        <Form formId={this.state.formId}/>
      </div>
	: ''
	return [select,form]
  }
}

export default App;
